body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#header {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0rem;
  background-color: rgb(98, 98, 98);
  color: white;
  flex-direction: column;
  max-width: 70rem;
}

#nameDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-left: 1rem;
}

#telDiv {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-left: 1rem;
}

#contactDiv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(49, 179, 184);
  color: white;
  width: 70rem;
  min-height: 15rem;
}

#root {
  width: 70rem;
  background-color: rgba(146, 211, 205, 0.267);
  box-shadow: 5px 5px 300px rgb(251, 251, 251);
}

#leftRow {
  flex-direction: column;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: url("https://boulderchamber.com/wp-content/uploads/2018/11/AdobeStock_157196449.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-box-reflect: below 1px
    linear-gradient(transparent, rgba(255, 255, 255, 0.2));
}

#indivCont {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(49, 179, 184);
  color: white;
  width: 19rem;
  min-height: 15rem;
}

#eduDiv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(46, 163, 167);
  color: white;
  width: 70rem;
  min-height: 15rem;
}

#schoolsDiv {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(46, 163, 167);
  color: white;
  width: 19rem;
  min-height: 15rem;
}

#skillsDiv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(40, 133, 136);
  color: white;
  width: 70rem;
  min-height: 15rem;
}

#indivSkills {
  margin-left: 1rem;
  color: white;
  width: 19rem;
  min-height: 15rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#location {
  max-width: 70rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(58, 57, 57);
  color: white;
}

#content {
  width: 50rem;
  height: 15rem;
  color: rgb(65, 65, 65);
  background-color: rgba(255, 255, 255, 0.657);
}

#content2 {
  width: 50rem;
  height: 15rem;
  color: rgb(65, 65, 65);
  background-color: rgba(255, 255, 255, 0.623);
}

#content3 {
  width: 50rem;
  height: 15rem;
  color: rgb(65, 65, 65);
  background-color: rgba(255, 255, 255, 0.623);
}

#changeHeaderBtn {
  background: none;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 30px;
  margin-top: 10px;
  margin-left: 55rem;
}
#changeHeaderBtn:hover {
  transform: scale(1.1);
}

#changeContact,
#changeSchool,
#changeSkills {
  background: none;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 20px;
  margin-left: 15rem;
}

#changeContact:hover,
#changeSchool:hover,
#changeSkills:hover {
  transform: scale(1.1);
}

#changeSkills {
  margin-top: 15px;
}

#changeAbout,
#changeWorkBtn,
#changeProjBtn {
  margin-top: 0px;
  margin-left: 1rem;
  background: none;
  outline: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
}

#changeAbout:hover,
#changeWorkBtn:hover,
#changeProjBtn:hover,
#changeLocationBtn:hover {
  transform: scale(1.1);
}

#aboutMeDiv,
#changeWork,
#projectsDiv {
  flex-direction: row;
  display: flex;
}

#changeLocationBtn {
  background: none;
  outline: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: white;
  margin-left: 1rem;
}

#inpProj,
#inpWork,
#inpMe,
#inpLoc,
#inpName,
#inpTel {
  display: none;
  width: 30rem;
  margin-left: 1rem;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.609);
}

#btnProj,
#btnWork,
#btnMe,
#btnLoc,
#btnName,
#btnTel {
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
}

#showUpDiv,
#showUpDivMe,
#changeLocDiv {
  display: flex;
  flex-direction: row;
}

#showUpDivWork {
  display: flex;
  flex-direction: row;
}

#submitAll {
  outline: none;
  border: none;
  background: none;
  font-size: 2rem;
  cursor: pointer;
}

#changeLocDiv {
  margin-left: 2rem;
  display: flex;
  margin-top: 1rem;
}

#skill1Inp,
#skill2Inp,
#highInp,
#collInp,
#emailInp,
#gitInp {
  display: none;
  border: none;
  outline: none;
  height: 1rem;
  background-color: rgba(255, 255, 255, 0.609);
}

#skill1Btn,
#skill2Btn,
#highBtn,
#collBtn,
#gitBtn,
#emailBtn {
  display: none;
  background: none;
  outline: none;
  border: none;
  color: white;
  font-size: 1.3rem;
}

#skill1,
#skill2 {
  display: flex;
  flex-direction: row;
}

#collDiv,
#highDiv,
#git,
#email {
  display: flex;
  flex-direction: row;
}
